<template>
	<div class="login-wrap">
		<div class="ms-title">后台管理系统</div>

		<div class="login">

			<el-form class="el-forms" :label-position="labelPosition" label-width="80px" :model="numberValidateForm" ref="numberValidateForm">

				<el-form-item label="学号" prop="age" :rules="[{ required: true, message: '学号不能为空'},{ type: 'number', message: '学号必须为数字值'}]">
					<el-input type="number" v-model.number="numberValidateForm.age" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码">
					<el-input v-model="formLabelAlign.type" type="password"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm">登陆</el-button>
					<el-button @click="resetForm">重置</el-button>
				</el-form-item>
			</el-form>

		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				labelPosition: 'right',
				formLabelAlign: {
					name: '',
					region: '',
					type: ''
				},
				numberValidateForm: {
					age: ''
				}
			}
		},

		methods: {
			submitForm() {
			
					if(this.numberValidateForm.age&&this.formLabelAlign.type) {
						sessionStorage.setItem("userName", this.numberValidateForm.age);
						this.$router.push('/home');
					} else {
						console.log('error submit!!');
						return false;
					}
				
			},
			resetForm(formName) {
				this.formLabelAlign.type = "";
				this.numberValidateForm.age = "";
			}
		}

	}
</script>

<style>
	.login-wrap {
		position: relative;
		width: 100%;
		height: 100%;
		background: rgb(50, 65, 87);
	}
	
	.login {
		background: rgb(50, 65, 87);
	}
	
	.ms-title {
		position: absolute;
		top: 50%;
		width: 100%;
		margin-top: -230px;
		text-align: center;
		font-size: 30px;
		color: #fff;
	}
	
	.el-forms {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 300px;
		height: 160px;
		margin: -150px 0 0 -190px;
		padding: 40px;
		border-radius: 5px;
		background: #fff;
	}
	
	.btn-login {
		width: 100%;
		height: 36px;
	}
</style>


